.header{
    background: #FFFFFF;
    box-shadow: 0px 1px 0px rgba(0, 0, 0, 0.12);
    padding-top: 8px;
    padding-bottom: 8px;
    position: fixed;
    top: 0;
    z-index: 99;
    position: fixed;
    width: 100%;
    .header_top{
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-between;
     &-nav{
            display: flex;
            align-items: center;
            flex-grow: 1;
            @include r1040{
                position: absolute;
                z-index: 98;
                left: -1000%;
                top: 0;
                flex-direction: column;
                margin-top: 73px;
                background-color: #fff;
                width: 100vw;
                height: 100vh;
                overflow: hidden;
                align-items: stretch;
                padding-left: 16px;
                padding-right: 16px;
                margin-bottom: 24px;
                transition: all .5s ease-in-out;
            }
            nav{
                margin-right: auto;
                @include r1040{
                    order: 1;
                    margin-right: 0;
                }
            }
        }
        #burger{
            display: none;
        }
        #burger:checked~.header_top-nav{
            left: 0;
        }
        #burger:checked~.logo .burger_inner .burger_label .burger_line:nth-child(1) {
            transform: translateY(6px) rotate(225deg);
        }
        #burger:checked~.logo .burger_inner .burger_label .burger_line:nth-child(3) {
            transform: translateY(-4px) rotate(-225deg);
        }
        #burger:checked~.logo .burger_inner .burger_label .burger_line:nth-child(2) {
            opacity: 0;
        }
    }
    
    .burger_inner{
        margin-right: 18px;
        display: none;
        @include r1040{
            display: block;
        }
        .burger_label {
            display: block;
            width: 24px;
            height: 24px;
            font-size: 32px;
            z-index: 10;
            position: relative;
            padding: 6px 3px;
            .burger_line{
                width: 18px;
                height: 2px;
                background-color: #000000;                
                border-radius: 15px;
                transition: all .5s ease-in-out;
                &:not(:last-child){
                    margin-bottom: 3px;
                }
            }
        }
    }
    .logo{
        z-index: 99;
        display: flex;
        align-items: center;
    }
    .nav_list{
        padding-left: 0;
    }
    .nav_list-item {
        display: inline-block;
        transition: 0.3s ease;
        margin-left: 32px;
        @include r1040{
            display: block;
            margin-left: 0;
        }
        @include h575{
            display: inline-block;
        }
        @include w356{
            display: block;
        }
        &:not(:first-child){
            @include r1040{
                margin-left: 0;
                border-top: 1px solid rgba(41, 50, 65, 0.12);;
            }
            @include h575{
                border-top: transparent;
                margin-left: 50px;
            }
            @include h530{
                margin-left: 10px;
            }
            @include w356{
                margin-left: 0;
            }
        }
        a{
            font-weight: 500;
            font-size: 14px;
            line-height: 20px;
            color: #293241;
            display: block;
            @include r1040{
                padding:24px 0;            
            }
            @include w356{
                padding:12px 0;
            }
            &:not(:first-child){
                @include w356{
                    padding:12px 0;
                }
            }
        }
    }
    .social{
        margin-left: auto;
        padding-top: 6px;
        @include r1040{
            order: 2;
            margin-left: 0;
            margin-top: auto;                                                                                                                                                                                                                                                                               
        }
        .social_link:first-child{
            margin-right: 20px;
        }
    }
    .searching{
        position: relative;
        margin-left: 32px;
        @include r1040{
            order: 0;
            margin-left: 0;
            margin-top: 24px;
            margin-bottom: 34px;
        }
        label{
            position: absolute;
            left: 15px;
            top: 11px;
        }
        .search_inp{
            max-width: 237px;
            background: #EFEFEF;
            border-radius: 8px;
            border:1px solid #EFEFEF;
            padding: 10px 48px;
            padding-right: 0;
            height: 40px;
            outline: none;
            color: #9E9EA7;
            &:active{
                background-color: #fff;
                color: #000000;
            }
            &:focus{
                background-color: #fff;
                color: #000000;
                border: 1px solid #E1E1E1;
            }
            @include r1040{
                max-width: 100%;
                width: 100%;
            }
        }
    }
    .color_mode{
        width: 40px;
        height: 40px;
        background: rgba(73, 85, 104, 0.08);
        border-radius: 8px;
        display: flex;
        justify-content: center;
        align-items: center;
        margin-left: 20px;
        cursor: pointer;
        z-index: 99;
    }
}


